<template>
    <div>
        <div class="panel panel-info">
            <div class="panel-body">
                <div>功能管理</div>
                <div class="buttonbox" v-show="!inforadmin">
                    <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="hasLoginComponents('sys:page:add')">新增</button>
                    <button type="button" class="btn btn-warning" @click="updateData" v-if="hasLoginComponents('sys:page:modify')">修改</button>
                    <button type="button" class="btn btn-danger" @click="deleteData" v-if="hasLoginComponents('sys:page:delete')">删除</button>
                    <div class="pull-right col-sm-3 search-input" v-if="hasLoginComponents('sys:page:search')">
                        <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                        <input type="text" class="form-control" placeholder="搜索用户"> 
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                        </div>
                    </div>
                </div>
                <tables v-show="!inforadmin" @tableCheck="onTableClick" :pageloginData="pageloginData"></tables>
                <add-infor  v-show="inforadmin" :statetitle="statetitle" :stateId="stateid" :checkData="checkData" @inforCall="inforAddClick"></add-infor>
            </div>
        </div>
    </div>
</template>
<script>
import tables from "@/components/table.vue";
import addInfor from "@/components/addinfor.vue";
export default {
  data:function(){
    return {
        checkData:[],
        inforadmin:false,
        searchData:{data:""},
        pageloginData:{
            id:this.stateid,
        }
    };
  },
 props:{
     stateid:String,
     statetitle:String
 },
  components: {
    tables,
    addInfor
  },
  methods:{
    onTableClick(data){
        this.checkData=data;
    },
      //判断是否当前页面是否存在按钮权限
    hasLoginComponents(perm){
      var vm=this;
       return vm.$api.opreate.hasLoginComponents(vm,perm);
    },
    inforAddClick(bool) {
      this.inforadmin=bool;
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm,'修改')) {
          this.inforadmin=true;
      }
    },
    // 删除按钮按下
    deleteData() {
        var vm = this;
        var checkData=vm.checkData;
      if (vm.$api.opreate.TESTcheck(vm,'删除'))  {
        var id = vm.$layer.confirm(
          '<div class="layer divtext"><i class="icon icon-question"></i>是否删除</div>',
          () => {
            vm.$layer.close(id);
            var datas = {
              id: checkData[0].id,
              states: {
                id: vm.stateId
              }
            };
            vm.$axios
              .post(vm.HOST + "deleteData.action", datas)
              .then(Response => {
                var res = Response.data;
                if (res.code == "0") {
                  vm.$layer.alert(
                    '<div class="layer divtext"><i class="icon icon-success"></i>删除成功</div>'
                  );
                } else {
                  vm.$layer.alert(
                    '<div class="layer divtext"><i class="icon icon-error"></i>' +
                      res.msg +
                      "</div>"
                  );
                }
              })
              .catch(error => {
                vm.$layer.alert(
                  '<div class="layer divtext"><i class="icon icon-warning"></i>网络错误，请检查网络是否畅通</div>'
                );
              });
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick(){
      var vm=this;
      if(vm.searchData.data!=""){

      }else{
         vm.$layer.alert(
          '<div class="layer divtext"><i class="icon icon-warning"></i>请输入用户名后搜索</div>'
        );
      }
    },
  },
   watch:{
      stateid(news){
         this.pageloginData.id=news;
      }
  }
};
</script>

<style>
</style>



